.App {
    text-align: center;
    animation: fade-in .5s;
    height: auto;
    width: 100%;
    overflow-x: hidden;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

.header {
    width: 100vw;
    height: 8rem;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-size: 1rem;
    color: white;
    padding: 0 8rem;
    font-family: "Quicksand", sans-serif;
    background-color: transparent;
    z-index: 2;
    position: relative;
}

.header h4 {
    font-size: 1.25rem;
    font-weight: 500;
}

.header h4 span {
    font-size: 1.125rem;
    color: #689d94;
}

.home {
    background-position: 40% 10%;
    margin-top: -8rem;
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 100%;
    color: white;
    padding: 8rem;
    font-family: "Quicksand", sans-serif;
    overflow: hidden;
    background-size: 200%;
    background-color: #282c34;
    /*color: #689d94;*/
    animation: backgrounInMoveMain 20s ease-in-out infinite;
}

.home div {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    text-align: left;
    margin: 4rem;
}

.home div h1 {
    margin-bottom: 1rem;
    font-weight: 600;
    font-size: 2.25rem;
    color: #689d94;
}

.home div h2 {
    margin-bottom: 4rem;
    font-weight: 600;
    font-size: 2.25rem;
    color: #689d94;
}

.home div h3 {
    font-size: 1.25rem;
    color: white;
}

.home div h2.title {
    font-size: 1.5rem;
    margin: 1rem 0;
    color: #689d94;
}
.home .addtonation {
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
    color: white;
    align-self: flex-end;
}

@keyframes backgrounInMoveMain {
    0% {
        background-position: 20% 10%;
        background-size: 160%;
    }
    50% {
        background-position: 40% 10%;
        background-size: 200%;
    }
    100% {
        background-position: 20% 10%;
        background-size: 160%;
    }
}